.card-list {
  padding: 0 20px;
  margin-bottom: 40px;

}

@media screen and (min-width: 700px) {
  .card-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px 20px;
    grid-template-areas: 
      "card1 card1 card1"
      "card2 card2 card3"
      "card2 card2 card3"
      "card4 card5 card5"
      "card4 card6 card6"
      "card7 card7 card8"
      "card7 card7 card8"
      "card9 card10 card10"
      "card9 card11 card11"; 

      margin-bottom: 0;
  }
} 

@media screen and (min-width: 700px) and (max-width: 800px) {
  .card-list {
    padding: 0;
  }
}

@media screen and (min-width: 1180px) {
  .card-list {
    gap: 40px;
    padding-right: 40px;
  }
}

